import React from 'react';
import PropTypes from 'prop-types';
import Box from '../../../../../common/src/components/Box';
import Text from '../../../../../common/src/components/Text';
import Heading from '../../../../../common/src/components/Heading';
import Container from '../../../../../common/src/components/UI/Container';
import SectionWrapper from './fonc.style';
import foncImage from 'common/src/assets/image/steps/Step_1.jpg';
import { useStaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-Image';
import first from 'common/src/assets/image/fonctionnement/1.jpg';
import second from 'common/src/assets/image/fonctionnement/2.jpg';
const FoncSection = ({
  secTitleWrapper,
  secText,
  secHeading,
  secDes,
  topHeading
}) => {
  const Data = useStaticQuery(graphql`
    query {
      saasClassicJson {
        IMAGES_FONCTIONNEMENT {
          first {
            childImageSharp {
              fluid {
                src
              }
            }
          }
          second {
            childImageSharp {
              fluid {
                src
              }
            }
          }
          third {
            childImageSharp {
              fluid {
                src
              }
            }
          }
          fourth {
            childImageSharp {
              fluid {
                src
              }
            }
          }
        }
      }
    }
  `);

  return (
    <SectionWrapper>
      <Container>
        <Box className="sectionBox" {...secTitleWrapper}>
          <Text {...secText} content="LA BASE DE CONTACTS MUSIQUE ULTIME" />
          <div className="imagetext">
            <img
              className="image"
              src={
                Data.saasClassicJson.IMAGES_FONCTIONNEMENT.first.childImageSharp
                  .fluid.src
              }
              alt="fonctionnementImage"
            />
            <div className="text">
              <Heading {...secHeading} content="Contact Major" />
              <Text
                {...secDes}
                content="Major Contacts est une puissante base de donnes de contact mdias. Un annuaire essentiel des contacts de l'industrie musicale Franaise qui vous offre lopportunit unique dentrer en contact avec des directeurs artistiques, talents scouts et labels managers de France."
              />
              <Text
                {...secDes}
                content="Il est utilis par des artistes, des groupes, des managers, des producteurs, des musiciens et bien dautres personnes, ne perdez pas de temps : soyez le prochain."
              />
            </div>
          </div>

          <div className="textimage">
            <img
              className="image"
              src={
                Data.saasClassicJson.IMAGES_FONCTIONNEMENT.second
                  .childImageSharp.fluid.src
              }
              alt="fonctionnementImage"
            />
            <div className="text">
              <Heading
                {...secHeading}
                content="9 artistes sur 10 narrivent pas  trouver les contacts cl de lindustrie musicale. En faites-vous partie ?"
              />
              <Text
                {...secDes}
                content="Comment faire pour que mes chansons passent  la radio ? Que contient un accord d'enregistrement ? O puis-je obtenir un financement ? Si vous avez des
            questions importantes sur l'industrie de la musique, ne cherchez pas plus loin que nos contacts."
              />
              <Text
                {...secDes}
                content="Il existe des contacts gratuits en ligne qui soient obsoltes avec d'anciennes informations, ou qui ne vous fournissent qu'une liste partielle de contacts. Ces listes sont mises  jour annuellement ou semestriellement avec peu ou pas de changements du tout."
              />
              <Text
                {...secDes}
                content="Saviez-vous quune personne sur trois changerait de poste dans ce segment ? L'industrie des mdias ne s'arrte jamais, et avec les gens qui dmnagent, prennent leur retraite et quittent l'industrie, il peut tre difficile de suivre le rythme"
              />
            </div>
          </div>

          <div className="imagetext">
            <img
              className="image"
              src={
                Data.saasClassicJson.IMAGES_FONCTIONNEMENT.third.childImageSharp
                  .fluid.src
              }
              alt="fonctionnementImage"
            />
            <div className="text">
              <Heading
                {...secHeading}
                content="Le guide complet pour faire dcoller votre carrire"
              />
              <Text
                {...secDes}
                content="Chez Major Contacts nous avons pass d'innombrables annes  rassembler les contacts les plus complexes de l'industrie de la musique. Aujourdhui, nous avons une quipe de recherche ddie  la mise  jour de notre fil d'actualit avec les derniers mouvements mdiatiques. Le but tant de vous fournir les contacts les plus  jour, prcis et les plus complets de l'industrie de la musique. commencer par les grands labels de France comme Initial Artist Services, Roche Musique, Because Music, Idol et plus encore."
              />
              <Text
                {...secDes}
                content="Des journalistes locaux aux diffuseurs nationaux, Major Contacts a le bon contact mdia pour votre histoire."
              />
            </div>
          </div>

          <div className="textimage">
            <img
              className="image"
              src={
                Data.saasClassicJson.IMAGES_FONCTIONNEMENT.fourth
                  .childImageSharp.fluid.src
              }
              alt="fonctionnementImage"
            />
            <div className="text">
              <Heading
                {...secHeading}
                content="L'annuaire essentiel des contacts de l'industrie musicale franaise que les artistes musicaux mritent"
              />
              <Text
                {...secDes}
                content="Que vous soyez un label indpendant, un artiste, un chanteur, un musicien, un rappeur, un producteur, un manager, un diteur, un ingnieur du son, un agent, une grande maison de disques, un distributeur ou un entrepreneur : De la rservation de concerts  l'envoi de dmos  des labels de disques ou  la planification de votre propre sortie dEP."
              />
              <Text
                {...secDes}
                content="Il s'agit de contacts dans des maisons de disque, des socits de relations publiques, de nouveaux blogs et magazines musicaux, de DJs et de producteurs radio, de promoteurs de concerts tablis, de managers, d'diteurs de musique et d'agences de synchronisation, ainsi que d'organisateurs de festivals et dvnements."
              />
            </div>
          </div>
        </Box>
      </Container>
    </SectionWrapper>
  );
};

FoncSection.propTypes = {
  secTitleWrapper: PropTypes.object,
  secText: PropTypes.object,
  secHeading: PropTypes.object,
  topHeading: PropTypes.object,
  secDes: PropTypes.object
};

FoncSection.defaultProps = {
  secTitleWrapper: {
    mb: ['30px', '50px']
  },
  secText: {
    as: 'span',
    display: 'block',
    textAlign: 'center',
    fontSize: '14px',
    letterSpacing: '0.15em',
    fontWeight: '700',
    color: '#ff4362',
    mb: '12px'
  },
  topHeading: {
    // textAlign: 'center',
    fontSize: ['20px', '24px', '36px', '36px'],
    fontWeight: '700',
    color: '#fff',
    letterSpacing: '-0.025em',
    mb: '0',
    ml: 'auto',
    mr: 'auto',
    lineHeight: '1.12',
    width: '500px',
    maxWidth: '100%'
  },
  secHeading: {
    // textAlign: 'center',
    fontSize: ['20px', '24px', '24px'],
    fontWeight: '700',
    color: '#fff',
    letterSpacing: '-0.025em',
    mt: '30px',
    mb: '20px',
    // ml: 'auto',
    // mr: 'auto',
    lineHeight: '1.12',
    // width: '540px',
    maxWidth: '100%'
  },
  secDes: {
    fontSize: ['14px', '15px', '16px'],
    color: '#ccc',
    lineHeight: '1.705',
    ml: 'auto',
    mr: 'auto',
    width: ['700px'],
    maxWidth: '100%'
    // textAlign: 'center'
  }
};

export default FoncSection;
